import React from 'react'

function Home (props) {
  return (
    <div>
      <img src="https://i.imgur.com/ZhOGxki.png" className="img-fluid" id="homepage" alt="Responsive image"></img>
      <h1 id="name">Hi, Im Katie. </h1>
      <p>Im a Shakespearean-trained actress turned Full-Stack Software Engineer.  Ive done everything from leading tours at Radio City Music Hall (ask me about the time I yelled at G-Eazy) to tech recruiting for a startup acquired by Microsoft.  At the end of the day, all of my stories and experiences have the same theme: I love a good process.  I am methodical, detail-oriented and hungry for my next challenge (and tacos) If youre looking for someone who knows web development and the hiring process with technical expertise  lets chat.</p>
    </div>
  )
}

export default Home



